import React, { useState, useEffect } from "react";
import { Grid } from "@material-ui/core";

import { useForm, Form } from "../components/useForm";
import * as employeeService from "../services/employeeService";
import Controls from "../components/controls/Controls";

const statusItems = [
	{ id: "enabled", title: "Enabled" },
	{ id: "disabled", title: "Disabled" },
];

const initialFValues = {
	id: 0,
	userName: "",
	email: "",
	userId: "",
	city: "",
	status: "enabled",
	userRole: "",
	branchId: "",
	// hireDate: new Date(),
	isPermanent: false,
};

export default function AddUser() {
	const validate = (fieldValues = values) => {
		let temp = { ...errors };
		if ("userName" in fieldValues)
			temp.userName = fieldValues.userName ? "" : "This field is required.";
		if ("email" in fieldValues)
			temp.email = /$^|.+@.+..+/.test(fieldValues.email)
				? ""
				: "Email is not valid.";
		if ("userId" in fieldValues)
			temp.userId =
				fieldValues.userId.length > 9 ? "" : "Minimum 10 numbers required.";
		if ("userRole" in fieldValues)
			temp.userRole =
				fieldValues.userRole.length !== 0 ? "" : "This field is required.";
		if ("branchId" in fieldValues)
        temp.branchId =
        fieldValues.branchId.length !== 0 ? "" : "This field is required.";
		setErrors({
			...temp,
		});

		if (fieldValues == values) return Object.values(temp).every((x) => x == "");
	};

	const { values, setValues, errors, setErrors, handleInputChange, resetForm } =
		useForm(initialFValues, true, validate);

	const handleSubmit = (e) => {
		e.preventDefault();
		if (validate()) {
			employeeService.insertEmployee(values);
			resetForm();
		}
	};

	return (
		<Form onSubmit={handleSubmit}>
			<Grid container>
				<Grid item md={6}>
					<Controls.Input
						name='userName'
						label='User Name'
						value={values.userName}
						onChange={handleInputChange}
						error={errors.userName}
					/>
					<Controls.Input
						label='Email ID'
						name='email'
						value={values.email}
						onChange={handleInputChange}
						error={errors.email}
					/>
					<Controls.Input
						label='User ID'
						name='userId'
						value={values.userId}
						onChange={handleInputChange}
						error={errors.userId}
					/>
					<Controls.Input
						label='City'
						name='city'
						value={values.city}
						onChange={handleInputChange}
					/>
				</Grid>
				<Grid item md={6}>
					<Controls.RadioGroup
						name='status'
						label='Status'
						value={values.status}
						onChange={handleInputChange}
						items={statusItems}
					/>
					<Controls.Select
						name='userRole'
						label='User Role'
						value={values.userRole}
						onChange={handleInputChange}
						options={employeeService.getUserRole()}
						error={errors.userRole}
					/>
                    		<Controls.Select
						name='branchId'
						label='Branch ID'
						value={values.branchId}
						onChange={handleInputChange}
						options={employeeService.getBranchId()}
						error={errors.branchId}
					/>
					{/* <Controls.DatePicker
                        name="hireDate"
                        label="Hire Date"
                        value={values.hireDate}
                        onChange={handleInputChange}
                    /> */}
					<Controls.Checkbox
						name='isPermanent'
						label='Permanent Employee'
						value={values.isPermanent}
						onChange={handleInputChange}
					/>

					<div>
						<Controls.Button type='submit' text='Add User' />
						<Controls.Button text='Reset' color='default' onClick={resetForm} />
					</div>
				</Grid>
			</Grid>
		</Form>
	);
}
